import React from "react";
import dummyPhoto from "../../../image/dummyphto.png";

import SquirrelArcher from "../../../image/NFT metadata/Squirrel Archer.png";
import SquirrelHealer from "../../../image/NFT metadata/Squirrel Healer.png";
import SquirrelNecromancer from "../../../image/NFT metadata/Squirrel Necromancer.png";
import SquirrelWarrior from "../../../image/NFT metadata/Squirrel Warrior.png";
import SquirrelWizard from "../../../image/NFT metadata/Squirrel Wizard.png";

const Squirrel = ({ name }) => {
  const nftImages = [
    {
      image: SquirrelArcher,
      title1: "Archer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 300,
      rarity: "44th",
      date: "5/5/23",
      mintNumber: "18th",
    },
    {
      image: SquirrelHealer,
      title1: "Healer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 240,
      rarity: "31st",
      date: "9/15/23",
      mintNumber: "37th",
    },

    {
      image: SquirrelNecromancer,
      title1: "Necromancer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 60,
      rarity: "8th",
      date: "12/15/23",
      mintNumber: "50th",
    },
    {
      image: SquirrelWarrior,
      title1: "Warrior",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 360,
      rarity: "49th",
      date: "7/7/23",
      mintNumber: "27th",
    },

    {
      image: SquirrelWizard,
      title1: "Wizard",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 240,
      rarity: "31st",
      date: "2/10/23",
      mintNumber: "6th",
    },
  ];

  return (
    <>
      <div className="container mx-auto">
        <div className="">
          <h3 className="text-xl md:text-start text-center">{name}</h3>
          <h3 className="text-base md:text-start text-center">
            Guardians Of The Forest
          </h3>
        </div>

        <div className="grid xl:grid-cols-4 lg:grid-cols-3 md:grid-cols-2 md:place-items-start place-items-center my-5 gap-14 mt-10">
          {nftImages?.map((item, index) => (
            <div className="text-center" key={index}>
              {/* <p className="custom-font-termina my-1">
                Mint Week : {item?.mintNumber}
              </p> */}
              <img src={item.image} alt="" className="px-2 md:px-0" />
              <p className="text-xl custom-font-termina mt-1">{item.title1}</p>
              <p>
                {item.title2} : {item.quantity}
              </p>
              <p>Rarity : {item.rarity}</p>
              {Number(item?.date.split("/")[0]) === 1 ||
              Number(item?.date.split("/")[0]) === 2 ||
              Number(item?.date.split("/")[0]) === 3 ? (
                <p>
                  {item.title3} :{" "}
                  {new Date(item?.date)?.toDateString().slice(4)}
                </p>
              ) : (
                <p>{item.title3} : TBD</p>
              )}
              {/* <p>Value is USD : {item.quantity * 28}</p> */}
            </div>
          ))}
        </div>
      </div>
    </>
  );
};

export default Squirrel;
