import React from "react";

import FrogArcher from "../../../image/NFT metadata/Frog Archer.png";
import FrogBard from "../../../image/NFT metadata/Frog Bard.png";
import FrogHealer from "../../../image/NFT metadata/Frog Healer.png";
import FrogNecromancer from "../../../image/NFT metadata/Frog Necromancer.png";
import FrogPirate from "../../../image/NFT metadata/Frog Pirate.png";
import FrogWarrior from "../../../image/NFT metadata/Frog Warrior.png";
import FrogWizard from "../../../image/NFT metadata/Frog Wizard.png";

const Frog = ({ name }) => {
  const nftImages = [
    {
      image: FrogArcher,
      title1: "Archer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 220,
      rarity: "28th",
      date: "6/23/23",
      mintNumber: "25th",
    },

    {
      image: FrogBard,
      title1: "Bard",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 55,
      rarity: "5th",
      date: "3/3/23",
      mintNumber: "9th",
    },

    {
      image: FrogHealer,
      title1: "Healer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 220,
      rarity: "28th",
      date: "4/21/23",
      mintNumber: "16th",
    },

    {
      image: FrogNecromancer,
      title1: "Necromancer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 55,
      rarity: "5th",
      date: "11/10/23",
      mintNumber: "45th",
    },

    {
      image: FrogPirate,
      title1: "Pirate",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 55,
      rarity: "5th",
      date: "9/22/23",
      mintNumber: "38th",
    },

    {
      image: FrogWarrior,
      title1: "Warrior",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 220,
      rarity: "28th",
      date: "4/7/23",
      mintNumber: "14th",
    },

    {
      image: FrogWizard,
      title1: "Wizard",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 275,
      rarity: "43rd",
      date: "8/4/23",
      mintNumber: "31st",
    },
  ];

  return (
    <>
      <div className="container mx-auto">
        <div className="">
          <h3 className="text-xl md:text-start text-center">{name}</h3>
          <h3 className="text-base md:text-start text-center">
            Guardians Of The Forest
          </h3>
        </div>

        <div className="grid xl:grid-cols-4 lg:grid-cols-3 md:grid-cols-2 md:place-items-start place-items-center my-5 gap-14 mt-10">
          {nftImages?.map((item, index) => (
            <div className="text-center" key={index}>
              {/* <p className="custom-font-termina my-1">
                Mint Week : {item?.mintNumber}
              </p> */}
              <img src={item.image} alt="" className="px-2 md:px-0" />
              <p className="text-xl custom-font-termina mt-1">{item.title1}</p>
              <p>
                {item.title2} : {item.quantity}
              </p>
              <p>Rarity : {item.rarity}</p>
              {Number(item?.date.split("/")[0]) === 1 ||
              Number(item?.date.split("/")[0]) === 2 ||
              Number(item?.date.split("/")[0]) === 3 ? (
                <p>
                  {item.title3} :{" "}
                  {new Date(item?.date)?.toDateString().slice(4)}
                </p>
              ) : (
                <p>{item.title3} : TBD</p>
              )}
              {/* <p>Value is USD : {item.quantity * 28}</p> */}
            </div>
          ))}
        </div>
      </div>
    </>
  );
};

export default Frog;
