import React from "react";
import { useEffect } from "react";
import { NavLink } from "react-router-dom";
import CartoonfoxImage from "../../Asset/Images/cartoon-fox.png";

const Collections = () => {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <>
      <div className="mx-auto container flex flex-col justify-center items-center">
        <h1 className="mt-28 mb-10 text-4xl">Collections</h1>
      </div>

      <div className="mx-auto container my-5 px-4">
        <h3 className="text-2xl mx-auto">Echo Forest Brand Collections</h3>
        <div className="grid md:grid-cols-2 my-5 gap-10">
          <div>
            <img
              src={CartoonfoxImage}
              alt="foximage"
              className="md:rounded-2xl w-full rounded-md md:h-96 md:px-0 px-2"
            />
          </div>

          <div className="text-justify ">
            <h5 className="text-lg">Guardians Of The Forest</h5>

            <p className="py-2">
              The Guardian of the Forest collection consists of 10,000 unique
              NFTs, each representing a character in the fantasy world called
              Echoverse. Every NFT in our collection is a combination of 1 of 10
              different forest animals, with 1 of 7 different character classes.
              In total there are 52 different animal/class combinations that
              will be released throughout the 2023 calendar year. Each animal
              is also equipped with anywhere from 2-4 times that vary in rarity
              from common to unique.
            </p>

            <p className="py-2">
              The utility of the Echo Forest NFT comes in two forms; earning
              $EXP token and participating in the Echo Forest DAO.
            </p>

            <button className="my-8 mx-auto">
              <NavLink
                className="rounded-lg bg-black text-white p-5 cursor-pointer hover:bg-white hover:text-black box-border border border-transparent hover:border-black hover:translate-1 duration-500 hover:shadow-xl"
                to="/collections/guardians-of-the-forest"
              >
                View Collections
              </NavLink>
            </button>
          </div>
        </div>
      </div>
    </>
  );
};

export default Collections;
