import React from "react";

import BeeArcher from "../../../image/NFT metadata/Bee Archer.png";
import BeeHealer from "../../../image/NFT metadata/Bee Healer.png";
import BeeNecromancer from "../../../image/NFT metadata/Bee Necromancer.png";
import BeeWarrior from "../../../image/NFT metadata/Bee Warrior.png";
import BeeWizard from "../../../image/NFT metadata/Bee Wizard.png";

const Bee = ({ name }) => {
  const nftImages = [
    {
      image: BeeArcher,
      title1: "Archer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 390,
      rarity: "50th",
      date: "7/21/23",
      mintNumber: "29th",
    },

    {
      image: BeeHealer,
      title1: "Healer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 260,
      rarity: "38th",
      date: "9/29/23",
      mintNumber: "39th",
    },

    {
      image: BeeNecromancer,
      title1: "Necromancer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 65,
      rarity: "12th",
      date: "2/17/23",
      mintNumber: "7th",
    },

    {
      image: BeeWarrior,
      title1: "Warrior",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 325,
      rarity: "48th",
      date: "11/17/23",
      mintNumber: "46th",
    },

    {
      image: BeeWizard,
      title1: "Wizard",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 260,
      rarity: "38th",
      date: "4/14/23",
      mintNumber: "15th",
    },
  ];

  return (
    <>
      <div className="container mx-auto">
        <div className="">
          <h3 className="text-xl md:text-start text-center">{name}</h3>
          <h3 className="text-base md:text-start text-center">
            Guardians Of The Forest
          </h3>
        </div>

        <div className="grid xl:grid-cols-4 lg:grid-cols-3 md:grid-cols-2 md:place-items-start place-items-center my-5 gap-14 mt-10">
          {nftImages?.map((item, index) => (
            <div className="text-center" key={index}>
              {/* <p className="custom-font-termina my-1">Mint Week : {item?.mintNumber}</p> */}
              <img src={item.image} alt="" className="px-2 md:px-0" />
              <p className="text-xl custom-font-termina mt-1">{item.title1}</p>
              <p>
                {item.title2} : {item.quantity}
              </p>
              <p>Rarity : {item.rarity}</p>
              {Number(item?.date.split("/")[0]) === 1 ||
              Number(item?.date.split("/")[0]) === 2 ||
              Number(item?.date.split("/")[0]) === 3 ? (
                <p>
                  {item.title3} :{" "}
                  {new Date(item?.date)?.toDateString().slice(4)}
                </p>
              ) : (
                <p>{item.title3} : TBD</p>
              )}
              {/* <p>Value is USD : {item.quantity * 28}</p> */}
            </div>
          ))}
        </div>
      </div>
    </>
  );
};

export default Bee;
