import React from "react";
import BirdArcher from "../../../image/NFT metadata/Bird Archer.png";
import BirdHealer from "../../../image/NFT metadata/Bird Healer.png";
import BirdNecromancer from "../../../image/NFT metadata/Bird Necromancer.png";
import BirdWarrior from "../../../image/NFT metadata/Bird Warrior.png";
import BirdWizard from "../../../image/NFT metadata/Bird Wizard.png";

const Bird = ({ name }) => {
  const nftImages = [
    {
      image: BirdArcher,
      title1: "Archer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 300,
      rarity: "44th",
      date: "11/24/23",
      mintNumber: "47th",
    },
    {
      image: BirdHealer,
      title1: "Healer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 200,
      rarity: "24th",
      date: "5/19/23",
      mintNumber: "20th",
    },

    {
      image: BirdNecromancer,
      title1: "Necromancer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 50,
      rarity: "1st",
      date: "2/3/23",
      mintNumber: "5th",
    },
    {
      image: BirdWarrior,
      title1: "Warrior",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 250,
      rarity: "36th",
      date: "3/10/23",
      mintNumber: "10th",
    },

    {
      image: BirdWizard,
      title1: "Wizard",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 200,
      rarity: "24th",
      date: "9/8/23",
      mintNumber: "36th",
    },
  ];

  return (
    <>
      <div className="container mx-auto">
        <div className="">
          <h3 className="text-xl md:text-start text-center">{name}</h3>
          <h3 className="text-base md:text-start text-center">
            Guardians Of The Forest
          </h3>
        </div>

        <div className="grid xl:grid-cols-4 lg:grid-cols-3 md:grid-cols-2 md:place-items-start place-items-center my-5 gap-14 mt-10">
          {nftImages?.map((item, index) => (
            <div className="text-center" key={index}>
              {/* <p className="custom-font-termina my-1">
                Mint Week : {item?.mintNumber}
              </p> */}
              <img src={item.image} alt="" className="px-2 md:px-0" />
              <p className="text-xl custom-font-termina mt-1">{item.title1}</p>
              <p>
                {item.title2} : {item.quantity}
              </p>
              <p>Rarity : {item.rarity}</p>
              {Number(item?.date.split("/")[0]) === 1 ||
              Number(item?.date.split("/")[0]) === 2 ||
              Number(item?.date.split("/")[0]) === 3 ? (
                <p>
                  {item.title3} :{" "}
                  {new Date(item?.date)?.toDateString().slice(4)}
                </p>
              ) : (
                <p>{item.title3} : TBD</p>
              )}
              {/* <p>Value is USD : {item.quantity * 28}</p> */}
            </div>
          ))}
        </div>
      </div>
    </>
  );
};

export default Bird;
