import React from "react";

import WolfArcher from "../../../image/NFT metadata/Wolf Archer.png";
import WolfHealer from "../../../image/NFT metadata/Wolf Healer.png";
import WolfNecromancer from "../../../image/NFT metadata/Wolf Necromancer.png";
import WolfWarrior from "../../../image/NFT metadata/Wolf Warrior.png";
import WolfWizard from "../../../image/NFT metadata/Wolf Wizard.png";

const Wolf = ({ name }) => {
  const nftImages = [
    {
      image: WolfArcher,
      title1: "Archer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 175,
      rarity: "21st",
      date: "9/1/23",
      mintNumber: "35th",
    },

    {
      image: WolfHealer,
      title1: "Healer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 105,
      rarity: "16th",
      date: "1/27/23",
      mintNumber: "1st",
    },

    {
      image: WolfNecromancer,
      title1: "Necromancer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 70,
      rarity: "13th",
      date: "4/28/23",
      mintNumber: "17th",
    },

    {
      image: WolfWarrior,
      title1: "Warrior",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 210,
      rarity: "26th",
      date: "12/29/23",
      mintNumber: "52nd",
    },

    {
      image: WolfWizard,
      title1: "Wizard",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 140,
      rarity: "18th",
      date: "6/30/23",
      mintNumber: "26th",
    },
  ];

  return (
    <>
      <div className="container mx-auto">
        <div className="">
          <h3 className="text-xl md:text-start text-center">{name}</h3>
          <h3 className="text-base md:text-start text-center">
            Guardians Of The Forest
          </h3>
        </div>

        <div className="grid xl:grid-cols-4 lg:grid-cols-3 md:grid-cols-2 md:place-items-start place-items-center my-5 gap-14 mt-10">
          {nftImages?.map((item, index) => (
            <div className="text-center" key={index}>
              {/* <p className="custom-font-termina my-1">
                Mint Week : {item?.mintNumber}
              </p> */}
              <img src={item.image} alt="" className="px-2 md:px-0" />
              <p className="text-xl custom-font-termina mt-1">{item.title1}</p>
              <p>
                {item.title2} : {item.quantity}
              </p>
              <p>Rarity : {item.rarity}</p>
              {Number(item?.date.split("/")[0]) === 1 ||
              Number(item?.date.split("/")[0]) === 2 ||
              Number(item?.date.split("/")[0]) === 3 ? (
                <p>
                  {item.title3} :{" "}
                  {new Date(item?.date)?.toDateString().slice(4)}
                </p>
              ) : (
                <p>{item.title3} : TBD</p>
              )}
              {/* <p>Value is USD : {item.quantity * 28}</p> */}
            </div>
          ))}
        </div>
      </div>
    </>
  );
};

export default Wolf;
