import React from "react";

import MothArcher from "../../../image/NFT metadata/Moth Archer.png";
import MothHealer from "../../../image/NFT metadata/Moth Healer.png";
import MothNecromancer from "../../../image/NFT metadata/Moth Necromancer.png";
import MothWarrior from "../../../image/NFT metadata/Moth Warrior.png";
import MothWizard from "../../../image/NFT metadata/Moth Wizard.png";

const Moth = ({ name }) => {
  const nftImages = [
    {
      image: MothArcher,
      title1: "Archer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 260,
      rarity: "38th",
      date: "10/13/23",
      mintNumber: "41st",
    },
    {
      image: MothHealer,
      title1: "Healer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 260,
      rarity: "38th",
      date: "3/17/23",
      mintNumber: "11th",
    },

    {
      image: MothNecromancer,
      title1: "Necromancer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 130,
      rarity: "17th",
      date: "6/9/23",
      mintNumber: "23rd",
    },
    {
      image: MothWarrior,
      title1: "Warrior",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 260,
      rarity: "38th",
      date: "7/28/23",
      mintNumber: "30th",
    },

    {
      image: MothWizard,
      title1: "Wizard",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 390,
      rarity: "50th",
      date: "11/3/23",
      mintNumber: "44th",
    },
  ];

  return (
    <>
      <div className="container mx-auto">
        <div className="">
          <h3 className="text-xl md:text-start text-center">{name}</h3>
          <h3 className="text-base md:text-start text-center">
            Guardians Of The Forest
          </h3>
        </div>

        <div className="grid xl:grid-cols-4 lg:grid-cols-3 md:grid-cols-2 md:place-items-start place-items-center my-5 gap-14 mt-10">
          {nftImages?.map((item, index) => (
            <div className="text-center" key={index}>
              {/* <p className="custom-font-termina my-1">
                Mint Week : {item?.mintNumber}
              </p> */}
              <img src={item.image} alt="" className="px-2 md:px-0" />
              <p className="text-xl custom-font-termina mt-1">{item.title1}</p>
              <p>
                {item.title2} : {item.quantity}
              </p>
              <p>Rarity : {item.rarity}</p>
              {Number(item?.date.split("/")[0]) === 1 ||
              Number(item?.date.split("/")[0]) === 2 ||
              Number(item?.date.split("/")[0]) === 3 ? (
                <p>
                  {item.title3} :{" "}
                  {new Date(item?.date)?.toDateString().slice(4)}
                </p>
              ) : (
                <p>{item.title3} : TBD</p>
              )}
              {/* <p>Value is USD : {item.quantity * 28}</p> */}
            </div>
          ))}
        </div>
      </div>
    </>
  );
};

export default Moth;
