import React from "react";

import FoxArcher from "../../../image/NFT metadata/Fox Archer.png";
import FoxHealer from "../../../image/NFT metadata/Fox Healer.png";
import FoxNecromancer from "../../../image/NFT metadata/Fox Necromancer.png";
import FoxWarrior from "../../../image/NFT metadata/Fox Warrior.png";
import FoxWizard from "../../../image/NFT metadata/Fox Wizard.png";

const Fox = ({ name }) => {
  const nftImages = [
    {
      image: FoxArcher,
      title1: "Archer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 175,
      rarity: "21st",
      date: "2/10/23",
      mintNumber: "4th",
    },
    {
      image: FoxHealer,
      title1: "Healer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 70,
      rarity: "13th",
      date: "6/2/23",
      mintNumber: "22nd",
    },

    {
      image: FoxNecromancer,
      title1: "Necromancer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 70,
      rarity: "13th",
      date: "10/27/23",
      mintNumber: "43rd",
    },
    {
      image: FoxWarrior,
      title1: "Warrior",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 210,
      rarity: "26th",
      date: "8/25/23",
      mintNumber: "34th",
    },

    {
      image: FoxWizard,
      title1: "Wizard",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 175,
      rarity: "21st",
      date: "12/22/23",
      mintNumber: "51st",
    },
  ];

  return (
    <>
      <div className="container mx-auto">
        <div className="">
          <h3 className="text-xl md:text-start text-center">{name}</h3>
          <h3 className="text-base md:text-start text-center">
            Guardians Of The Forest
          </h3>
        </div>

        <div className="grid xl:grid-cols-4 lg:grid-cols-3 md:grid-cols-2 md:place-items-start place-items-center my-5 gap-14 mt-10">
          {nftImages?.map((item, index) => (
            <div className="text-center" key={index}>
              {/* <p className="custom-font-termina my-1">
                Mint Week : {item?.mintNumber}
              </p> */}
              <img src={item.image} alt="" className="px-2 md:px-0" />
              <p className="text-xl custom-font-termina mt-1">{item.title1}</p>
              <p>
                {item.title2} : {item.quantity}
              </p>
              <p>Rarity : {item.rarity}</p>
              {Number(item?.date.split("/")[0]) === 1 ||
              Number(item?.date.split("/")[0]) === 2 ||
              Number(item?.date.split("/")[0]) === 3 ? (
                <p>
                  {item.title3} :{" "}
                  {new Date(item?.date)?.toDateString().slice(4)}
                </p>
              ) : (
                <p>{item.title3} : TBD</p>
              )}
              {/* <p>Value is USD : {item.quantity * 28}</p> */}
            </div>
          ))}
        </div>
      </div>
    </>
  );
};

export default Fox;
