import React from "react";

import MouseArcher from "../../../image/NFT metadata/Mouse Archer.png";
import MouseBard from "../../../image/NFT metadata/Mouse Bard.png";
import MouseHealer from "../../../image/NFT metadata/Mouse Healer.png";
import MouseNecromancer from "../../../image/NFT metadata/Mouse Necromancer.png";
import MousePirate from "../../../image/NFT metadata/Mouse Pirate.png";
import MouseWarrior from "../../../image/NFT metadata/Mouse Warrior.png";
import MouseWizard from "../../../image/NFT metadata/Mouse Wizard.png";

const Mouse = ({ name }) => {
  const nftImages = [
    {
      image: MouseArcher,
      title1: "Archer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 240,
      rarity: "31st",
      date: "8/18/23",
      mintNumber: "33rd",
    },

    {
      image: MouseBard,
      title1: "Bard",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 60,
      rarity: "8th",
      date: "2/3/23",
      mintNumber: "3rd",
    },
    {
      image: MouseHealer,
      title1: "Healer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 240,
      rarity: "31st",
      date: "12/8/23",
      mintNumber: "49th",
    },

    {
      image: MouseNecromancer,
      title1: "Necromancer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 60,
      rarity: "8th",
      date: "10/20/23",
      mintNumber: "42nd",
    },

    {
      image: MousePirate,
      title1: "Pirate",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 60,
      rarity: "8th",
      date: "6/16/23",
      mintNumber: "24th",
    },

    {
      image: MouseWarrior,
      title1: "Warrior",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 300,
      rarity: "44th",
      date: "5/12/23",
      mintNumber: "19th",
    },

    {
      image: MouseWizard,
      title1: "Wizard",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 240,
      rarity: "31st",
      date: "3/31/23",
      mintNumber: "13th",
    },
  ];

  return (
    <>
      <div className="container mx-auto">
        <div className="">
          <h3 className="text-xl md:text-start text-center">{name}</h3>
          <h3 className="text-base md:text-start text-center">
            Guardians Of The Forest
          </h3>
        </div>

        <div className="grid xl:grid-cols-4 lg:grid-cols-3 md:grid-cols-2 md:place-items-start place-items-center my-5 gap-14 mt-10">
          {nftImages?.map((item, index) => (
            <div className="text-center" key={index}>
              {/* <p className="custom-font-termina my-1">
                Mint Week : {item?.mintNumber}
              </p> */}
              <img src={item.image} alt="" className="px-2 md:px-0" />
              <p className="text-xl custom-font-termina mt-1">{item.title1}</p>
              <p>
                {item.title2} : {item.quantity}
              </p>
              <p>Rarity : {item.rarity}</p>
              {Number(item?.date.split("/")[0]) === 1 ||
              Number(item?.date.split("/")[0]) === 2 ||
              Number(item?.date.split("/")[0]) === 3 ? (
                <p>
                  {item.title3} :{" "}
                  {new Date(item?.date)?.toDateString().slice(4)}
                </p>
              ) : (
                <p>{item.title3} : TBD</p>
              )}
              {/* <p>Value is USD : {item.quantity * 28}</p> */}
            </div>
          ))}
        </div>
      </div>
    </>
  );
};

export default Mouse;
