import React from "react";

import OwlArcher from "../../../image/NFT metadata/Owl Archer.png";
import OwlBard from "../../../image/NFT metadata/Owl Bard.png";
import OwlHealer from "../../../image/NFT metadata/Owl Healer.png";
import OwlNecromancer from "../../../image/NFT metadata/Owl Necromancer.png";
import OwlPirate from "../../../image/NFT metadata/Owl Pirate.png";
import OwlWarrior from "../../../image/NFT metadata/Owl Warrior.png";
import OwlWizard from "../../../image/NFT metadata/Owl Wizard.png";

const Owl = ({ name }) => {
  const nftImages = [
    {
      image: OwlArcher,
      title1: "Archer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 150,
      rarity: "19th",
      date: "3/24/23",
      mintNumber: "12th",
    },

    {
      image: OwlBard,
      title1: "Bard",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 50,
      rarity: "1st",
      date: "12/1/23",
      mintNumber: "48th",
    },
    {
      image: OwlHealer,
      title1: "Healer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 250,
      rarity: "36th",
      date: "8/11/23",
      mintNumber: "32nd",
    },

    {
      image: OwlNecromancer,
      title1: "Necromancer",
      title2: "Quantity",
      title3: "Release Date",
      quantity: "50th",
      rarity: "1st",
      date: "10/6/23",
      mintNumber: "40th",
    },

    {
      image: OwlPirate,
      title1: "Pirate",
      title2: "Quantity",
      title3: "Release Date",
      quantity: "50th",
      rarity: '1st',
      date: "2/3/23",
      mintNumber: "2nd",
    },

    {
      image: OwlWarrior,
      title1: "Warrior",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 150,
      rarity: "19th",
      date: "2/24/23",
      mintNumber: "8th",
    },

    {
      image: OwlWizard,
      title1: "Wizard",
      title2: "Quantity",
      title3: "Release Date",
      quantity: 300,
      rarity: "44th",
      date: "5/26/23",
      mintNumber: "21st",
    },
  ];

  return (
    <>
      <div className="container mx-auto">
        <div className="">
          <h3 className="text-xl md:text-start text-center">{name}</h3>
          <h3 className="text-base md:text-start text-center">
            Guardians Of The Forest
          </h3>
        </div>

        <div className="grid xl:grid-cols-4 lg:grid-cols-3 md:grid-cols-2 md:place-items-start place-items-center my-5 gap-14 mt-10">
          {nftImages?.map((item, index) => (
            <div className="text-center" key={index}>
              {/* <p className="custom-font-termina my-1">
                Mint Week : {item?.mintNumber}
              </p> */}
              <img src={item.image} alt="" className="px-2 md:px-0" />
              <p className="text-xl custom-font-termina mt-1">{item.title1}</p>
              <p>
                {item.title2} : {item.quantity}
              </p>
              <p>Rarity : {item.rarity}</p>
              {Number(item?.date.split("/")[0]) === 1 ||
              Number(item?.date.split("/")[0]) === 2 ||
              Number(item?.date.split("/")[0]) === 3 ? (
                <p>
                  {item.title3} :{" "}
                  {new Date(item?.date)?.toDateString().slice(4)}
                </p>
              ) : (
                <p>{item.title3} : TBD</p>
              )}
              {/* <p>Value is USD : {item.quantity * 28}</p> */}
            </div>
          ))}
        </div>
      </div>
    </>
  );
};

export default Owl;
