import React, { useEffect } from "react";
import Bee from "./Bee/Bee";
import Bird from "./bird/Bird";
import Deer from "./deer/Deer";
import Fox from "./fox/Fox";
import Frog from "./frog/Frog";
import Moth from "./moth/Moth";
import Mouse from "./mouse/Mouse";
import Owl from "./owl/Owl";
import Squirrel from "./squirrel/Squirrel";
import Wolf from "./wolf/Wolf";
import AOS from "aos";

const Nft = () => {
  AOS.init();
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  
  return (
    <>
      <div className="container mx-auto flex justify-center items-center">
        <h1 className="mt-40 mb-10 text-4xl ">Guardians Of The Forest</h1>
      </div>

      {/* Bee */}

      <div className="py-20">
        <Bee name="Bee" />
      </div>

      {/* Bird */}
      <div className="py-20">
        <Bird name="Bird" />
      </div>

      <div className="py-20">
        <Deer name="Deer" />
      </div>
      <div className="py-20">
        <Fox name="Fox" />
      </div>

      <div className="py-20">
        <Frog name="Frog" />
      </div>
      <div className="py-20">
        <Moth name="Moth" />
      </div>
      <div className="py-20">
        <Mouse name="Mouse" />
      </div>
      <div className="py-20">
        <Owl name="Owl" />
      </div>
      <div className="py-20">
        <Squirrel name="Squirrel" />
      </div>
      <div className="py-20">
        <Wolf name="Wolf" />
      </div>
    </>
  );
};

export default Nft;
